@import url(https://fonts.googleapis.com/css?family=Raleway:300);
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1e2733;
  min-height: 100vh;
  font-family: 'Raleway', sans-serif;
  color: #ffffff; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }


div {
  position: absolute;
  z-index: 1;
  width: 100%; }

h1 {
  line-height: 500px;
  text-align: center;
  font-weight: 300; }
  h1 span {
    color: #ebc86e; }


nav {
  position: relative;
  top: 300px;
  z-index: 2;
  width: 200px;
  margin: 0 auto;
  text-align: center; }
  nav ul {
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center; }
  nav li {
    float: left;
    list-style: none;
    font-size: 24px; }

a {
  color: #ffffff; }
  a:hover {
    color: #ebc86e; }

